<template>
  <v-row align="center" justify="center">
    <div style="width: 500px;">
      <v-form ref="form" v-model="valid" :lazy-validation="lazy">
        <v-text-field v-model="username"  :rules="usernameRule" :label="$t('field._username')" required outlined dense></v-text-field>

        <v-text-field v-model="password" :rules="passwordRule" :label="$t('field._password')" required outlined dense></v-text-field>
        <v-checkbox
          v-model="checkbox"
          :rules="[v => !!v || 'You must agree to continue!']"
          :label="$t('_rememberme')"
          required
        ></v-checkbox>
        <v-btn color="error" class="mr-4" @click="reset">{{$t('btn._backHome')}}</v-btn>

        <v-btn color="warning" @click="frmsubmit">{{$t('btn._login')}}</v-btn>
      </v-form>
    </div>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    username: "",
    usernameRule: [
      (v) => !!v || "Name is required",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
    password: "",
    passwordRule: [
    //   (v) => !!v || "E-mail is required",
    //   (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    checkbox: false,
    lazy: false,
  }),

  methods: {
    frmsubmit() {
    //   this.$refs.form.validate();
      this.$router.push({path:this.username})
      console.log(this.$i18n.locale+'/'+this.username)
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
  },
};
</script>